import React from 'react'

import PropTypes from 'prop-types'

import './espionaje.css'

const Espionaje = (props) => {
  return (
    <div className={`espionaje-container ${props.rootClassName} `}>
      <img
        alt={props.image_alt}
        src={props.image_src}
        className="espionaje-image"
      />
      <h5 className="espionaje-text headline5">{props.title}</h5>
      <span className="espionaje-text1">{props.text}</span>
    </div>
  )
}

Espionaje.defaultProps = {
  rootClassName: '',
  title: 'Espionaje',
  text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed erat nibh tristique ipsum.',
  image_src: '/playground_assets/01.svg',
  image_alt: 'image',
}

Espionaje.propTypes = {
  rootClassName: PropTypes.string,
  title: PropTypes.string,
  text: PropTypes.string,
  image_src: PropTypes.string,
  image_alt: PropTypes.string,
}

export default Espionaje
